@import url(https://fonts.googleapis.com/css2?family=Pacifico&display=swap);
*{margin:0;box-sizing:border-box}header{background:#181818;display:-webkit-flex;display:flex;-webkit-flex-direction:row;flex-direction:row;-webkit-justify-content:space-between;justify-content:space-between}header .menu{display:-webkit-flex;display:flex;height:3em}header span{-webkit-align-items:center;align-items:center}header a{all:unset;padding:0.8em;margin:0 1em;background:#181818;cursor:pointer;transition:all 0.4s;color:#fff;font-family:'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;font-size:0.9em}header a:hover{color:#eaae00;transition:all 0.2s;box-shadow:rgba(0,0,0,0.479) 0px 0px 5px 5px}@media only screen and (max-width: 1023px){header a{font-size:1em}}@media only screen and (max-width: 768px){header *{margin:0;box-sizing:border-box}header a{font-size:0.8em}}@media only screen and (max-width: 450px){header a{font-size:0.7em}}

*{margin:0;box-sizing:border-box;width:100%}.movie{padding:3px;background:#181818;box-shadow:rgba(0,0,0,0.561) 10px 5px 5px;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center;width:auto;height:auto;border-radius:2%;cursor:pointer;position:relative;overflow:hidden}.movie ::-webkit-scrollbar{width:6px;height:4px;background:#e2e2e2;border-radius:4px}.movie ::-webkit-scrollbar-thumb:hover{background:#b3b3b3}.movie .movieTitle{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;color:#f0f0f0;height:4.4vh;font-size:0.8em;font-weight:bold;font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;text-shadow:1px 1px 1px rgba(0,0,0,0.1);overflow:hidden;border-radius:5px;text-align:center;word-wrap:break-word}.movie img{margin-top:0.2em;height:25vh;border-radius:5px;position:relative}.movie .average{background-color:#353535;border:2px solid #a8a8a8;border-radius:50%;margin:-2.7em 0.4em;position:absolute;width:2em}.movie .average p{color:#fffdfd;font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;font-weight:bolder;font-size:0.8em;text-align:center;margin:0.5em auto}.movie .average .green{color:#05b805;text-shadow:1px 1px 1px rgba(0,0,0,0.1)}.movie .average .orange{color:orange;text-shadow:1px 1px 1px rgba(0,0,0,0.1)}.movie .average .tag.red{color:#ff1001;text-shadow:1px 1px 1px rgba(0,0,0,0.1)}@media only screen and (max-width: 1440px){.movie .movieTitle{font-size:1em;height:5.8vh}}@media only screen and (max-width: 1024px){.movie .movieTitle{font-size:0.8em;height:5.6vh;overflow:hidden}.movie img{height:270px}}@media only screen and (max-width: 768px){.movie .movieTitle{font-size:0.8em;height:5.5vh}.movie img{height:270px}}@media only screen and (max-width: 450px){.movie .movieTitle{font-size:0.8em;height:5.3vh}.movie img{height:270px}}.moreDetails{position:absolute;background-color:white;bottom:0;left:0;right:0;padding:0.5rem;-webkit-transform:translateY(100%);transform:translateY(100%);transition:-webkit-transform 1s ease-in-out;transition:transform 1s ease-in-out;transition:transform 1s ease-in-out, -webkit-transform 1s ease-in-out;overflow:auto;max-height:100%;font-size:11px;font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif}.moreDetails h4{margin-top:1em}.movie:hover{-webkit-transform:scale(1.5);transform:scale(1.5);-webkit-backface-visibility:hidden;backface-visibility:hidden;z-index:1;background:rgba(54,54,54,0.685);box-shadow:rgba(0,0,0,0.774) 0px 0px 10px 7px}.movie:hover .moreDetails{-webkit-transform:translateY(0%);transform:translateY(0%)}

*{margin:0;box-sizing:border-box;width:100%}.titleMovieType{display:-webkit-flex;display:flex;-webkit-flex-direction:row;flex-direction:row;background:rgba(5,5,5,0.527);padding:0.6em 1.5em;color:#fffefe;-webkit-justify-content:flex-start;justify-content:flex-start;font-weight:lighter;font-size:0.8em;font-family:'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;text-shadow:rgba(0,0,0,0.774) 0px 2px 10px 7px}

*{margin:0;box-sizing:border-box}.contenedor{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:center;justify-content:center;width:100vw;position:absolute;z-index:1}.contenedorMovies{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:flex-start;justify-content:flex-start;margin:0}.titleMovieType{margin:0}.movies{display:grid;grid-template-columns:repeat(10, 1fr);grid-gap:1rem;grid-row-gap:80px;padding:1em;width:95%;height:90%;margin:4rem auto;transition:all 2s}@media only screen and (max-width: 1440px){.movies{grid-template-columns:repeat(5, 220px);grid-row-gap:8px;grid-gap:3.5rem;-webkit-justify-content:center;justify-content:center;width:100%;margin:2em auto}}@media only screen and (max-width: 1024px) and (orientation: portrait){.movies{grid-template-columns:repeat(auto-fill, 180px);-webkit-justify-content:center;justify-content:center;width:100%;margin:2em auto 7em}}@media only screen and (max-width: 768px){.movies .movies{grid-template-columns:repeat(auto-fill, 160px);grid-row-gap:200px;-webkit-justify-content:center;justify-content:center;width:100%;margin:30px;padding:2em;background-attachment:fixed;background-repeat:no-repeat}}@media only screen and (max-width: 450px){.movies .movies{grid-template-columns:repeat(auto-fill, 150px);grid-row-gap:200px;width:100%;padding:2em;background-attachment:fixed;background-repeat:no-repeat}.movies .contenedorMovies{-webkit-justify-content:center;justify-content:center}}

footer{background-color:#181818;padding:0.6em;bottom:0;position:fixed;z-index:2;display:-webkit-flex;display:flex;-webkit-justify-content:flex-end;justify-content:flex-end;-webkit-align-items:flex-end;align-items:flex-end;color:#fff}footer p{text-align:end;margin-right:2em;font-size:0.8em;font-family:'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif}

*{margin:0;box-sizing:border-box}.searchConteiner{margin:0;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:center;justify-content:center;position:absolute;z-index:1}.searchConteiner .searchResults{padding:0.6em 1.5em;text-align:left;color:white;-webkit-justify-content:center;justify-content:center;font-size:1.2em;font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;background:rgba(5,5,5,0.527)}.input{margin:0.8em 2em;-webkit-align-items:center;align-items:center;height:2.2em;width:25em;box-sizing:content-box;font-size:14px;border:1px solid #666;border-radius:5px;background:center no-repeat transparent;color:#ddd;outline:0}.result{display:grid;grid-template-columns:repeat(5, 1fr);grid-gap:1.5em;grid-row-gap:60px;padding:1em;width:50%;height:90%;transition:all 2s;margin:1.5rem auto}@media only screen and (max-width: 1024px){.result{grid-template-columns:repeat(auto-fill, 180px);width:100%;-webkit-justify-content:center;justify-content:center;margin:2em auto 8em}}@media only screen and (max-width: 768px){.result{grid-template-columns:repeat(auto-fill, 180px);grid-row-gap:10px;grid-gap:3rem;width:100%;-webkit-justify-content:center;justify-content:center}}@media only screen and (max-width: 450px){.result{grid-template-columns:repeat(auto-fill, 180px);width:100%;-webkit-justify-content:center;justify-content:center}}

.contenedorTituloInicio{padding:0;z-index:-1;position:relative;height:auto;width:78%;margin:2em 0 0 3em;font-family:'Pacifico', cursive;font-weight:300;font-size:5em;text-shadow:5px 5px 2px rgba(0,0,0,0.246);text-align:center;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center;-webkit-justify-content:space-evenly;justify-content:space-evenly}.word{z-index:1;width:50vw;-webkit-animation:showup 7s infinite;animation:showup 7s infinite}.word2{width:70vw;-webkit-animation:reveal 7s infinite;animation:reveal 7s infinite}@-webkit-keyframes showup{0%{opacity:0}20%{opacity:1}50%{opacity:1}70%{opacity:1}90%{opacity:1}100%{opacity:0}}@keyframes showup{0%{opacity:0}20%{opacity:1}50%{opacity:1}70%{opacity:1}90%{opacity:1}100%{opacity:0}}@-webkit-keyframes reveal{0%{opacity:0}20%{opacity:0}50%{opacity:1}80%{opacity:1}100%{opacity:0}}@keyframes reveal{0%{opacity:0}20%{opacity:0}50%{opacity:1}80%{opacity:1}100%{opacity:0}}@media only screen and (max-width: 1024px){.contenedorTituloInicio{margin:1em auto}}@media only screen and (max-width: 768px){.contenedorTituloInicio{margin:0.7em auto}.word{font-size:1em}.word2{font-size:1em}}@media only screen and (max-width: 450px){.contenedorTituloInicio{margin:1.2em auto}.word{font-size:0.6em}.word2{font-size:0.6em}}

